/* You can add global styles to this file, and also import other style files */
@font-face {
  font-family: "nanum";
  font-style: normal;
  font-weight: 400;
  src: url(./font/NanumSquareR.otf) format("opentype");
}
@font-face {
  font-family: "nanum-m";
  font-style: normal;
  font-weight: 500;
  src: url(./font/NanumSquare_acB.ttf) format("opentype");
}

@font-face {
  font-family: "nanum-b";
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  word-break: keep-all;
  src: url(./font/NanumSquareB.otf) format("opentype");
}

* {
  font-family: "nanum";
}

html,
body {
  font-size: 16px;
}

.modal-holder {
  animation-name: example;
  animation-duration: 0.3s;
}
@keyframes example {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.carousel-item {
  transition: opacity 0.7s ease !important;
  position: absolute !important;
  display: block !important;
  opacity: 0;
}

.carousel-item.active {
  position: relative !important;
  opacity: 1;
}

// 
.logo-box {
  display: flex;
  align-items: center;
  justify-items: center;
  padding-top: 16px;
}

.min-wrap {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.container-fluid .title-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}

.page-title {
  font-size: 1.375rem;
  margin-bottom: 0;
}

table {
  text-align: center;
}

.badge {
  padding: 0.375rem !important;
}

input {
  &:focus-visible {
    outline: none;
  }
}

button {
  font-size: 0.875rem !important;
  &:focus {
    box-shadow: none !important;
  }
}
.badge {
  font-size: 0.875rem !important;
}
option {
  &:disabled {
    display: none;
  }
}

//   
.no-list {
  height: calc(100vh - 43.75rem);
  display: flex;
  justify-content: center;
  align-items: center;
}

//   transparent
.page-item {
  .page-link {
    background-color: transparent;
  }
  &.disabled {
    .page-link {
      background-color: transparent !important;
    }
  }
}

//  
full-calendar {
  .btn-group {
    min-height: 39px;
    button {
      display: flex;
      align-items: center;
    }
  }
  &.fc .fc-daygrid-day-frame {
    cursor: pointer;
  }
}

.swal2-popup {
  padding-top: 43.5px !important;
  width: 255px !important;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  border-radius: 10px !important;
  .swal2-content {
    padding: 0;
    .swal2-html-container {
      font-family: "nanum";
      font-size: 16px;
      line-height: 24px;
      word-break: keep-all;
    }
  }
  // alert button
  .swal2-actions {
    gap: 8px;
    margin-top: 18.14px;
    button {
      margin: 0;
    }
    // .swal2-cancel {
    //   padding: 7px;
    // }
    .swal2-alert-btn {
      height: 28px;
      font-family: "nanum";
      font-size: 16px !important;
      line-height: 18px;
      color: #fff;
      border-radius: 5px;
      background: #2065ec;
    }
  }
}

.alert-close {
  position: absolute;
  top: 10px;
  right: 0.625rem;
  background-color: transparent !important;
  background: url(./assets/img/close-btn.png) no-repeat center center / 0.875rem 0.875rem !important;
}

.sidebar-enable.vertical-collpsed {
  .navbar-brand-box {
    padding: 0;
  }
  .cus-logo-box {
    max-width: 70px;
    padding: 0 1.4rem;
    img {
      object-fit: contain;
      width: 100%;
      height: auto;
    }
  }
}

@media (max-width: 991.98px) {
  body[data-sidebar="dark"] .navbar-brand-box {
    display: none;
  }

  .sidebar-enable.vertical-collpsed {
    .navbar-brand-box {
      display: block;
    }
  }
}

