//select
select {
  // min-width: 100px;
  // width: 11.25rem;
  height: 2.375rem;
  margin-right: 1rem;
  font-size: 0.9375rem;
  border: 0.0625rem solid #ced4da;
  appearance: none;
  padding-right: 30px;
  background: url(./assets/imgs/select.png) no-repeat right 5px center / 16px;
