//  
.modal-wrap {
  padding: 2.5rem 1.875rem;
  width: 100%;
  max-height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .modal-main-tit {
    font-size: 1.5rem;
    line-height: 1.75rem;
    font-family: "nanum-m";
    padding-bottom: 1.25rem;
  }

  .detail-view {
    width: 100%;
    border: 0.0625rem solid #dddddd;
    border-radius: 0.25rem;
    padding: 1.875rem 1.25rem;
    margin-bottom: 2.5rem;
    max-height: calc(100vh - 80px - 50px - 38px);
    overflow: scroll;
  }

  ul {
    width: 100%;

    li {
      padding-bottom: 1.25rem;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      &:last-child {
        padding-bottom: 0;
      }
      // 
      &.image-area {
        p {
          img {
            width: 100%;
            max-width: 12.5rem;
            cursor: pointer;
          }

          .x-icon {
            position: absolute;
            top: 0.3125rem;
            right: 0.625rem;
            font-size: 1.625rem;
          }
        }
        .img-wrap {
          width: 100% !important;
        }
      }
      //
    }
    p {
      margin: 0 !important;
      width: 69%;
      font-size: 1rem;
      line-height: 1.5rem;
      font-family: "nanum";
      color: #485056;
      text-align: left;
      word-break: break-all;
      padding: 0.375rem 0.625rem;
      &.tit {
        width: 29%;
        font-size: 1rem;
        line-height: 1.5rem;
        font-weight: 600;
        color: #333;
        margin-right: 2%;
        span {
          color: red;
        }
      }
      &.input-wrap {
        border: 0.0625rem solid #ddd;
        padding: 0.375rem 0.625rem;
        input {
          width: 100%;
        }
      }
      &.textarea {
        border: 0.0625rem solid #ddd;
        padding: 0.625rem;
        height: 18.75rem;
        textarea {
          width: 100%;
          height: 100%;
          resize: none;
        }
      }
    }
  }

  // 
  .user-state {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  .active-btn {
    width: 30%;
  }

  ul li.danger-area {
    align-items: center;
    p {
      display: flex;
      align-items: center;
    }
  }

  .image-area {
    p {
      display: flex;
      flex-wrap: wrap;
      gap: 0.4rem;
    }
  }

  //  
  .img-add {
    width: 69%;

    .add-btn {
      width: 100%;
      height: 15.625rem;
    }
    .img-wrap {
      width: 100%;
      height: 15.625rem;
      position: relative;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        cursor: pointer;
      }
      button {
        position: absolute;
        top: 0;
        right: 0;
        min-width: 1.875rem;
        height: 1.875rem;
        padding: 0rem;
        background: rgba(0, 0, 0, 0.8);
        color: #fff;
        i {
          font-size: 1.25rem;
        }
      }
    }
  }
  //

  button {
    min-width: 5rem;
    font-size: 0.9375rem;
  }
}

