tbody {
  min-height: calc(100vh - 56px - 86px - 59px - 220px - 352px);
}

.row,
.row-ej,
.tab-container {
  .col-12 {
    padding-left: 33px !important;
    padding-right: 33px !important;
  }
  .card {
    box-shadow: none;
    filter: drop-shadow(0px 6px 18px rgba(0, 0, 0, 0.06));
  }
  .card-body {
    padding: 0 !important;
  }
  thead {
    position: sticky;
    top: -0.3px;
    z-index: 100;
    background: #f5f7fb;
    tr {
      position: relative;
      &::after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: 1px;
        width: 100%;
        height: 1px;
        background: #e1e1e1;
        z-index: 100;
      }
    }
    th {
      padding: 14.25px 4px 16.25px;
      min-height: 48px;
      font-family: "nanum-b";
      font-size: 14px;
      line-height: 16px;
      vertical-align: middle !important;

      // border-bottom: 1px solid #e1e1e1 !important;
      border-bottom: 0 !important;
    }
  }

  tbody {
    background-color: #fff;
    tr {
      min-height: 64px;
      height: 64px;

      &.input-border {
        min-height: 64px;
        height: 64px;
        border: 1px solid #2967d3;
      }
    }
    td {
      white-space: pre-line;
      border: 1px solid transparent;
      border: 0;
      font-family: "nanum";
      font-size: 15px;
      line-height: 17px;
      vertical-align: middle;
      color: #475258;
      // white-space: nowrap;
      word-break: break-all;
    }
  }

  input {
    width: 100%;
    font-family: "nanum";
    font-size: 14px;
    line-height: 16px;
  }
  input:not([type="search"]),
  select {
    // border-color: #949494;
    border-radius: 3px;
  }
}

