.pagination-rounded {
  padding: 16px 16px 23px;
  width: 100%;
  .page-item {
    .page-link {
      font-size: 15px;
      line-height: 18px;
      color: #3a3a49;
      letter-spacing: -0.02em;
    }
    &.active {
      .page-link {
        color: #2967d3;
        border-radius: 5px !important;
        background-color: #fff;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.05));
      }
    }

    &:first-of-type {
      display: none;
    }
    &:nth-of-type(2) {
      a {
        color: transparent;
        background: url(./assets/img/icon-pagination.png) no-repeat center center / 5px 8px;
      }
    }
    &:nth-last-of-type(2) {
      a {
        color: transparent;
        background: url(./assets/img/icon-pagination.png) no-repeat center center / 5px 8px;
        transform: rotate(180deg);
      }
    }
    &:last-of-type {
      display: none;
    }
  }
}

