.modal-table {
  thead {
    position: sticky;
    top: 0;
    z-index: 100;
    background: #f5f7fb;

    th {
      position: relative;
      padding: 7px 0.3125rem;
      min-height: 1.875rem;
      font-family: "nanum-b";
      font-size: 13px;
      line-height: 0.9375rem;
      color: #131927;
      &:last-of-type {
        &::after {
          display: none;
        }
      }
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 100%;
        height: 100%;
        background-color: transparent;
        border-right: 1px solid #a8bec9 !important;
        z-index: 100;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      border: 1px solid #a8bec9;
      border-bottom: 1px solid #a8bec9 !important;
      z-index: 3;
      background-color: transparent;
    }
  }
  tbody {
    min-height: 1.875rem;
    tr {
      height: 1.875rem;
    }
    td {
      min-width: 5rem;
      font-family: "nanum";
      font-size: 0.875rem;
      line-height: 1rem;
      vertical-align: middle;
      color: #475258;
      border: 1px solid #e1e1e1;
      border-top: 0;
      padding: 0.375rem 0.3125rem;
    }
  }
}

