mbsc-datepicker {
  // 
  &.anly-cal {
    position: relative;

    width: 6.25rem;
    height: 2rem;
    &::before {
      z-index: 20;
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0.25rem;
      width: 1.25rem;
      height: 1.25rem;

      background: url(./assets/img/calendar-icon.png) no-repeat center center / 100% 100%;
    }

    .mbsc-ios.mbsc-form-control-wrapper:before,
    .mbsc-ios.mbsc-form-control-wrapper:after {
      border: 0;
    }
    .mbsc-ios.mbsc-textfield-wrapper-underline {
      border-radius: 4px;

      border: 1px solid #949494;
    }
    .mbsc-ios.mbsc-textfield {
      padding: 0.25rem;
      margin-left: 1.5625rem;
      width: 6.25rem;
      height: 2rem;
      padding: 0;
      font-family: "nanum";
      font-size: 0.875rem;
      line-height: 0.875rem;
      color: #475258;
    }
  }

  //  
  &.list-cal {
    position: relative;
    width: 6.25rem;
    height: 2rem;
    &::before {
      z-index: 20;
      content: "";
      position: absolute;
      left: -2.875rem;
      top: 50%;
      transform: translateY(-50%);
      width: 1.25rem;
      height: 1.25rem;

      background: url(./assets/img/calendar-icon.png) no-repeat center center / 100% 100%;
    }

    .mbsc-ios.mbsc-form-control-wrapper:before,
    .mbsc-ios.mbsc-form-control-wrapper:after {
      border: 0;
    }
    .mbsc-ios.mbsc-textfield-wrapper-underline {
      border-radius: 4px;
      border: 1px solid #949494;
    }

    .mbsc-ios.mbsc-textfield {
      position: relative;
      right: -2.25rem;
      top: 2px;

      width: 6.25rem;
      height: 2rem;
      padding: 0;
      font-family: "nanum";
      font-size: 0.875rem;
      line-height: 0.875rem;
      color: #475258;
    }
  }
}

